import React, { useContext, useEffect } from 'react'
import { BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom'
import { createBrowserHistory } from 'history'
import { useTranslation } from 'react-i18next'
import { makeStyles } from '@material-ui/core/styles'
import config from 'config'
import { CoinAcceptorFactory, DartboardFactory } from 'shared/factories'
import { Emitter, SoundEffectPlayer } from 'shared/services'
import { GameMenuView, GameView, OptionsView, Typography } from 'components'
import { CreditContext, BackgroundImageContext, OAuthContext } from 'components/contexts'
import styles from './styles'

let state = {
  game: {},
  players: []
}
const history = createBrowserHistory()
const useStyles = makeStyles(styles)

export default function App() {
  const { isArcadeMode } = config
  const oauth = useContext(OAuthContext)
  const { credit, increaseCredit, decreaseCredit } = useContext(CreditContext)
  const { backgroundImage } = useContext(BackgroundImageContext)
  const { t } = useTranslation()
  const classes = useStyles({ backgroundImageUrl: backgroundImage.url })

  useEffect(() => {
    const dartboard = DartboardFactory.create(config.dartboard)
    const coinAcceptor = CoinAcceptorFactory.create(config.coinAcceptor)
    const handleCoinInsert = () => {
      increaseCredit(1)
      SoundEffectPlayer.play('coin')
    }
    const handleGameOver = () => decreaseCredit(state.game.credit * state.players.length)

    if (dartboard.isBluetoothConnection()) {
      history.push('/options')
    } else {
      dartboard.connect()
    }
    if (isArcadeMode()) {
      coinAcceptor.connect()
      Emitter.on('COIN_INSERT', handleCoinInsert)
      Emitter.on('GAME_OVER', handleGameOver)
    }

    return () => {
      dartboard.disconnect()
      if (isArcadeMode()) {
        coinAcceptor.disconnect()
        Emitter.off('COIN_INSERT', handleCoinInsert)
        Emitter.off('GAME_OVER', handleGameOver)
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <div className={classes.root}>
      {
        oauth.isLogined &&
        <Router>
          <Switch>
            <Route path='/game-menu/:step' render={() => (
                <React.Fragment>
                  <GameMenuView appState={state} />
                  {
                    isArcadeMode() && <Typography className={classes.credit} variant='h4'>{t('credit', { credit })}</Typography>
                  }
                </React.Fragment>
              )
            }/>
            <Route path="/game" render={() =>
              <GameView game={state.game} players={state.players} />
            }/>
            <Route path="/options" render={() =>
              <OptionsView />
            }/>
            <Route render={() => <Redirect to='/game-menu/games' />} />
          </Switch>
        </Router>
      }
    </div>
  )
}
