import { useState } from 'react'
import { CreditContext } from 'components/contexts'

export default function CreditProvider(props) {
  const [credit, setCredit] = useState(0)
  return (
    <CreditContext.Provider value={{
      credit,
      increaseCredit: credits => typeof credits === 'number' && credits > 0 && setCredit(prevCredit => prevCredit + credits),
      decreaseCredit: credits => typeof credits === 'number' && credits > 0 && setCredit(prevCredit => prevCredit - credits)
    }}>
      {props.children}
    </CreditContext.Provider>
  )
}
