import { useContext, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { BackgroundImageContext } from 'components/contexts'
import { CountUpGameView, NinjaCountUpGameView, ThreeDartsOnePrizeGameView, ZeroOneGameView } from 'components/games'

export default function GameView(props = { game: {}, players: [] }) {
  const { game, players } = props
  const history = useHistory()
  const { setRandomBackgroundImage } = useContext(BackgroundImageContext)
  useEffect(() => {
    game.name !== 'ninja-count-up' && setRandomBackgroundImage()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  switch(game.name) {
    case 'zero-one':
      return <ZeroOneGameView players={players} score={game.score} rounds={game.rounds} />
    case 'count-up':
      return <CountUpGameView players={players} rounds={game.rounds} />
    case 'ninja-count-up':
      return <NinjaCountUpGameView players={players} rounds={game.rounds} />
    case '3-darts-1-prize':
      return <ThreeDartsOnePrizeGameView players={players} rounds={game.rounds} />
    default:
      history.push('/')
      return null
  }
}
