import { Emitter } from 'shared/services'

export default class KeyboardCoinAcceptor {
  connected = false

  connect = () => new Promise(resolve => {
    document.addEventListener('keypress', this.handleCoinInsert)
    this.connected = true
    resolve()
  })

  disconnect = () => new Promise(resolve => {
    document.removeEventListener('keypress', this.handleCoinInsert)
    this.connected = false
    resolve()
  })

  handleCoinInsert = event => {
    const { ctrlKey, key } = event
    if (ctrlKey === true && (key === 'i' || key === 'I')) {
      Emitter.emit('COIN_INSERT')
    }
  }
}
