import { KeyboardCoinAcceptor, MouseCoinAcceptor } from 'shared/services/coinacceptors'

export default class CoinAcceptorFactory {
  static create(name) {
    switch(name) {
      case 'mouse':
        return new MouseCoinAcceptor()
      case 'keyboard':
      default:
        return new KeyboardCoinAcceptor()
    }
  }
}
