import React, { useContext, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { MenuList, MenuItem } from '@material-ui/core'
import i18n from 'i18n'
import { useTranslation } from 'react-i18next'
import config from 'config'
import { PlayerManager, SoundEffectPlayer, Speech } from 'shared/services'
import { MenuItemSelector, Typography } from 'components'
import { CreditContext } from 'components/contexts'

const playerManager = new PlayerManager(config.players)

export default function PlayerSelectionView(props) {
  const { appState  } = props
  const history = useHistory()
  const { credit } = useContext(CreditContext)
  const { players } = playerManager
  const [selectedIndex, setSelectedIndex] = useState(appState.players.length ? appState.players.length - 1 : 0)
  const { t } = useTranslation()
  const speech = new Speech(i18n.language)
  const { isArcadeMode } = config
  const handlePlayersSelect = players => {
    const remainingCredit = credit - appState.game.credit * players.length
    if (isArcadeMode() && remainingCredit < 0) {
      SoundEffectPlayer.play('warn')
                       .then(() => speech.speak(t('moreCoins', { coins: Math.abs(remainingCredit) })))
    } else {
      SoundEffectPlayer.play('game_on')
      appState.players = players
      history.push('/game')
    }
  }
  const handleBackButtonClick = () => {
    SoundEffectPlayer.play('back')
    history.push('/game-menu/games')
  }
  return (
    <MenuList>
      <MenuItemSelector selectedIndex={selectedIndex} setSelectedIndex={setSelectedIndex}>
        {
          [...Array(players.length).keys()].map(i => (
            <MenuItem key={i}
                      selected={i === selectedIndex}
                      onMouseOver={() => SoundEffectPlayer.play('hover') }
                      onClick={() => handlePlayersSelect(players.slice(0, i + 1))}>
              <Typography variant='h2'>{t(`${i + 1} player${i > 0 ? 's' : ''}`)}</Typography>
              {
                // WORKAROUND: use the style param to override the styles.
                isArcadeMode() && <Typography style={{ marginLeft: '24px' }} variant='h5'>
                                    {t('requiredCredits', { credit: appState.game.credit * players.slice(0, i + 1).length })}
                                  </Typography>
              }
            </MenuItem>
          ))
        }
        <MenuItem selected={players.length === selectedIndex}
                  onMouseOver={() => SoundEffectPlayer.play('hover') }
                  onClick={handleBackButtonClick}>
          <Typography variant='h3'>{t('Back')}</Typography>
        </MenuItem>
      </MenuItemSelector>
    </MenuList>
  )
}
