import { Emitter } from 'shared/services'

export default class MouseCoinAcceptor {
  connected = false

  connect = () => new Promise(resolve => {
    document.ondblclick = () => Emitter.emit('COIN_INSERT')
    this.connected = true
    resolve()
  })

  disconnect = () => new Promise(resolve => {
    document.ondblclick = null
    this.connected = false
    resolve()
  })
}
