import React, { useContext, useEffect, useMemo } from 'react'
import { useHistory } from 'react-router-dom'
import { observer } from 'mobx-react-lite'
import { Emitter, SoundEffectPlayer } from 'shared/services'
import { ClassicDartGameView, GameMessagesView } from 'components'
import { CreditContext } from 'components/contexts'
import ThreeDartsOnePrizeGame from './ThreeDartsOnePrizeGame'

export default function ThreeDartsOnePrizeGameView(props) {
  const { players, rounds } = props
  const history = useHistory()
  const { increaseCredit } = useContext(CreditContext)

  players.length === 0 && history.push('/game-menu')

  const game = useMemo(() => new ThreeDartsOnePrizeGame(players, { rounds }), [players, rounds])
  useEffect(() => {
    if (game.isInitialized()) {
      const { theme: originalSoundEffectTheme } = SoundEffectPlayer
      SoundEffectPlayer.setTheme('3-darts-1-prize')
      SoundEffectPlayer.play('bgm')
      game.start()
      game.onGameOver = () => {
        // The prize is 2 credits for each winning.
        game.winners.length && increaseCredit(game.winners.length * 2)
        Emitter.once('CHANGE_BUTTON_CLICK', () => {
          game.clearGameMessages()
          game.addGameMessage('back')
          game.stop()
          SoundEffectPlayer.stop('bgm')
          SoundEffectPlayer.setTheme(originalSoundEffectTheme)
          Emitter.emit('GAME_OVER')
          history.push('/game-menu')
        })
      }
    }
  }, [increaseCredit, game, history])

  const GameView = observer(({ game }) => {
    const {
      currentPlayer: player,
      currentPlayerGameScore: gameScore,
      currentPlayerDartScores: dartScores,
      currentPlayerScoreboard: scoreboard,
      players,
      gameScores
    } = game.state
    return <ClassicDartGameView name='3 darts 1 prize'
                                player={player}
                                gameScore={gameScore}
                                dartScores={dartScores}
                                scoreboard={scoreboard}
                                players={players}
                                gameScores={gameScores} />
  })
  const MessagesView = observer(({ game }) => {
    const { messages } = game.state
    return <GameMessagesView messages={messages} onClose={game.handleGameMessageOnClose} />
  })
  return (
    <React.Fragment>
      <GameView game={game} />
      <MessagesView game={game} />
    </React.Fragment>
  )
}
